import React,{ useRef, useEffect } from 'react'
import "./NHeader.css";
import "./NContent.css";
import DarkMode from './../Components/DarkMode';
import Navbar from '../Components/Navbar';
import handleGsap from '../Utils/handleGsap';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const NHome = () => {

  //content
  const wrapPhoto = useRef(null);
  const content = useRef(null);

  useEffect(() => {
      if (window.innerWidth > 1025 && wrapPhoto.current) {
        gsap.fromTo(wrapPhoto.current, 
            {
              y: 0,      // Tambahkan kurung kurawal untuk objek properti
              x: 0,
              scale: 1,  // Tambahkan kurung kurawal untuk objek properti
            },
            {
              y: 430,
              x: 100,
              scale: 1,
              duration: 8,
              scrollTrigger: {
                  trigger: wrapPhoto.current,
                  start: 'top 30%',
                  end: 'top -30%',
                  scrub: 4,
                  toggleActions: 'restart none none none',
              }
            }
          );          
      }
  },[]);
  //content

  return (
    <div id='NHome' className='container-fluid m-0 p-0'>
        {/* <DarkMode /> */}
        <Navbar page='home' content={content}/>

        <div className='h-header position-relative d-flex flex-wrap align-content-center'>

            {/* <img className='wrap-photo-header'>
            </img> */}

            <div className='h-wrap-title'>
                <h1 className='h-title-header'>An enthusiastic</h1>
                <h1 className='h-title-header'>website developer</h1>
                <h1 className='h-title-header'>from Indonesia.</h1>
            </div>

            <img ref={wrapPhoto} alt='Coding' className='h-wrap-photo-header border ms-auto position-relative rounded-2' style={{zIndex: '3'}}>
            </img>


            <div className='h-blur-circle-1 rounded-circle position-absolute'></div>
            <div className='h-blur-circle-2 rounded-circle position-absolute'></div>

        </div>

        <div ref={content} className='h-content position-relative' style={{zIndex: '2'}}>

            <p data-aos='fade-up' className='m-0 text-light title-about d-block'>
                Empowering <b>brands to shine</b> in the digital age. 
                Together we will establish the new norm. 
                No frills, always at the forefront.
            </p>

            <p data-aos='fade-up' className='m-0 mt-4 title-about d-block'>
                The fusion of my enthusiasm for <b>design</b>, 
                <b> coding</b>, and <b>interaction</b> places me in a 
                distinctive position in the realm of 
                <b> web developer</b>.

                <a href='#awda' className='mt-4 text-light d-block'>About me?</a>
            </p>

           

        </div>

    </div>
  )
}

export default NHome